 bold;\n\tcolor: var(--color-main-text);\n}\n.text-error[data-v-e9ed8570] {\n\tcolor: var(--color-error);\n}\n.text-success[data-v-e9ed8570] {\n\tcolor: var(--color-success);\n}\n.quick-actions[data-v-e9ed8570] {\n\tdisplay: grid;\n\tgrid-template-columns: repeat(2, 1fr);\n\tgap: 10px;\n}\n.quick-actions .button-vue[data-v-e9ed8570] {\n\twidth: 100%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: flex-start;\n\tpadding: 10px 15px;\n\tfont-size: 1em;\n\tborder: none;\n\tborder-radius: var(--border-radius-element);\n\tcursor: pointer;\n\ttransition: all 0.2s ease;\n}\n.button-vue--primary[data-v-e9ed8570] {\n\tbackground-color: var(--color-primary);\n\tcolor: var(--color-primary-text);\n}\n.button-vue--primary[data-v-e9ed8570]:hover {\n\tbackground-color: var(--color-primary-hover);\n}\n.button-vue--tertiary[data-v-e9ed8570] {\n\tbackground-color: transparent;\n\tcolor: var(--color-primary);\n\tborder: 1px solid var(--color-primary);\n}\n.button-vue--tertiary[data-v-e9ed8570]:hover {\n\tbackground-color: var(--color-primary-light);\n}\n.button-vue .icon-contacts[data-v-e9ed8570],\n.button-vue .icon-folder[data-v-e9ed8570],\n.button-vue .icon-checkmark[data-v-e9ed8570],\n.button-vue .icon-files[data-v-e9ed8570] {\n\tmargin-right: 8px;\n}\n.recent-clients-list[data-v-e9ed8570] {\n\tlist-style: none;\n\tpadding: 0;\n\tmargin: 0;\n}\n.recent-client-item[data-v-e9ed8570] {\n\tdisplay: flex;\n\talign-items: center;\n\tpadding: 10px 0;\n\tborder-bottom: 1px solid var(--color-border);\n}\n.recent-client-item[data-v-e9ed8570]:last-child {\n\tborder-bottom: none;\n}\n.avatar[data-v-e9ed8570] {\n\twidth: 40px;\n\theight: 40px;\n\tborder-radius: 50%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tcolor: white;\n\tfont-weight: bold;\n\tfont-size: 0.9em;\n\tflex-shrink: 0;\n}\n.avatar[data-v-e9ed8570] {\n\twidth: 40px;\n\theight: 40px;\n\tborder-radius: 50%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tcolor: white;\n\tfont-weight: bold;\n\tfont-size: 0.9em;\n\tflex-shrink: 0;\n}\n.recent-client-info[data-v-e9ed8570] {\n\tmargin-left: 10px;\n}\n.recent-client-name[data-v-e9ed8570] {\n\tfont-weight: bold;\n\tcolor: var(--color-main-text);\n}\n.recent-client-email[data-v-e9ed8570] {\n\tfont-size: 0.9em;\n\tcolor: var(--color-text-maxcontrast);\n}\n.empty-content[data-v-e9ed8570] {\n\tdisplay: flex;\n\tflex-direction: column;\n\talign-items: center;\n\tjustify-content: center;\n\tpadding: 40px 20px;\n\ttext-align: center;\n}\n.empty-content__icon[data-v-e9ed8570] {\n\tfont-size: 3em;\n\tcolor: var(--color-text-maxcontrast);\n\tmargin-bottom: 10px;\n\topacity: 0.5;\n}\n.empty-content__text[data-v-e9ed8570] {\n\tcolor: var(--color-text-maxcontrast);\n\tfont-size: 1em;\n}\n.counter-bubble[data-v-e9ed8570] {\n\tdisplay: inline-flex;\n\talign-items: center;\n\tjustify-content: center;\n\tmin-width: 24px;\n\theight: 24px;\n\tpadding: 0 8px;\n\tbackground-color: var(--color-primary);\n\tcolor: var(--color-primary-text);\n\tborder-radius: 12px;\n\tfont-size: 0.85em;\n\tfont-weight: bold;\n}\n.alert-card--error[data-v-e9ed8570] {\n\tbackground-color: var(--color-error);\n\tcolor: var(--color-error-text);\n}\n.alert-card--warning[data-v-e9ed8570] {\n\tbackground-color: var(--color-warning);\n\tcolor: var(--color-warning-text);\n}\n.alert-card--info[data-v-e9ed8570] {\n\tbackground-color: var(--color-info);\n\tcolor: var(--color-info-text);\n}\n.alert-card .dashboard-card__header h3[data-v-e9ed8570] {\n\tcolor: inherit;\n}\n.alert-list[data-v-e9ed8570] {\n\tlist-style: none;\n\tpadding: 0;\n\tmargin: 0;\n}\n.alert-item[data-v-e9ed8570] {\n\tdisplay: flex;\n\tjustify-content: space-between;\n\talign-items: center;\n\tpadding: 10px 0;\n\tborder-bottom: 1px solid rgba(255, 255, 255, 0.1);\n}\n.alert-item[data-v-e9ed8570]:last-child {\n\tborder-bottom: none;\n}\n.alert-item__title[data-v-e9ed8570] {\n\tfont-weight: bold;\n}\n.alert-item__meta[data-v-e9ed8570] {\n\tfont-size: 0.9em;\n\topacity: 0.8;\n}\n.alert-item__amount[data-v-e9ed8570] {\n\tfont-weight: bold;\n\twhite-space: nowrap;\n}\n.empty-content[data-v-e9ed8570] {\n\tdisplay: flex;\n\tflex-direction: column;\n\talign-items: center;\n\tjustify-content: center;\n\tpadding: 40px 20px;\n\ttext-align: center;\n}\n.empty-content__icon[data-v-e9ed8570] {\n\tfont-size: 3em;\n\tcolor: var(--color-text-maxcontrast);\n\tmargin-bottom: 10px;\n\topacity: 0.5;\n}\n.empty-content__text[data-v-e9ed8570] {\n\tcolor: var(--color-text-maxcontrast);\n\tfont-size: 1em;\n}\n.counter-bubble[data-v-e9ed8570] {\n\tdisplay: inline-flex;\n\talign-items: center;\n\tjustify-content: center;\n\tmin-width: 24px;\n\theight: 24px;\n\tpadding: 0 8px;\n\tbackground-color: var(--color-primary);\n\tcolor: var(--color-primary-text);\n\tborder-radius: 12px;\n\tfont-size: 0.85em;\n\tfont-weight: bold;\n}\n@media screen and (max-width: 1024px) {\n.dashboard-main-row[data-v-e9ed8570] {\n\t\tgrid-template-columns: 1fr;\n}\n}\n@media screen and (max-width: 768px) {\n.dashboard-stats-grid[data-v-e9ed8570] {\n\t\tgrid-template-columns: 1fr;\n}\n.mini-stats-grid[data-v-e9ed8570] {\n\t\tgrid-template-columns: 1fr;\n}\n.quick-actions[data-v-e9ed8570] {\n\t\tgrid-template-columns: 1fr;\n}\n.dashboard-header[data-v-e9ed8570] {\n\t\tflex-direction: column;\n\t\talign-items: flex-start;\n}\n.dashboard-date[data-v-e9ed8570] {\n\t\tmargin-top: 10px;\n}\n}\n", "",{"version":3,"sources":["webpack://./src/components/Dashboard.vue"],"names":[],"mappings":";AAknBA;CACC,aAAa;CACb,oBAAoB;AACrB;AAEA;CACC,aAAa;CACb,8BAA8B;CAC9B,uBAAuB;CACvB,mBAAmB;AACpB;AAEA;CACC,gBAAgB;CAChB,iBAAiB;CACjB,6BAA6B;CAC7B,kBAAkB;AACnB;AAEA;CACC,gBAAgB;CAChB,oCAAoC;AACrC;AAEA;CACC,gBAAgB;CAChB,oCAAoC;CACpC,mBAAmB;AACpB;AAEA;CACC,aAAa;CACb,2DAA2D;CAC3D,SAAS;CACT,mBAAmB;AACpB;AAEA;CACC,aAAa;CACb,8BAA8B;CAC9B,SAAS;AACV;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,SAAS;AACV;AAEA;CACC,8CAA8C;CAC9C,2CAA2C;CAC3C,aAAa;CACb,wCAAwC;AACzC;AAEA;CACC,aAAa;CACb,8BAA8B;CAC9B,mBAAmB;CACnB,mBAAmB;CACnB,gBAA