rder);\r\n\tborder-radius: var(--border-radius-element);\r\n\tbackground-color: var(--color-background-dark);\r\n\tcolor: var(--color-main-text);\r\n\tfont-size: 14px;\r\n\tbox-sizing: border-box;\n}\n.form-control[data-v-3b088ed5]:focus {\r\n\toutline: none;\r\n\tborder-color: var(--color-primary);\n}\n.form-control[data-v-3b088ed5]::placeholder {\r\n\tcolor: var(--color-text-maxcontrast);\n}\n.form-actions[data-v-3b088ed5] {\r\n\tdisplay: flex;\r\n\tjustify-content: flex-end;\r\n\tgap: 12px;\r\n\tmargin-top: 24px;\r\n\tpadding-top: 20px;\r\n\tborder-top: 1px solid var(--color-border);\r\n\tposition: sticky;\r\n\tbottom: 0;\r\n\tbackground-color: var(--color-main-background);\r\n\tz-index: 10;\n}\n.button-vue[data-v-3b088ed5] {\r\n\tdisplay: inline-flex;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n\tpadding: 8px 16px;\r\n\tborder-radius: var(--border-radius-small);\r\n\tfont-size: 14px;\r\n\tcursor: pointer;\r\n\ttransition: all 0.2s ease;\r\n\ttext-decoration: none;\r\n\twhite-space: nowrap;\r\n\tbox-sizing: border-box;\r\n\tborder: 1px solid transparent;\n}\n.button-vue[data-v-3b088ed5]:disabled {\r\n\topacity: 0.6;\r\n\tcursor: not-allowed;\n}\n.button-vue--success[data-v-3b088ed5] {\r\n\tbackground-color: var(--color-success);\r\n\tcolor: var(--color-success-text);\n}\n.button-vue--success[data-v-3b088ed5]:hover:not(:disabled) {\r\n\topacity: 0.9;\n}\n.button-vue--secondary[data-v-3b088ed5] {\r\n\tbackground-color: transparent;\r\n\tcolor: var(--color-main-text);\r\n\tborder-color: var(--color-border);\n}\n.button-vue--secondary[data-v-3b088ed5]:hover:not(:disabled) {\r\n\tbackground-color: var(--color-background-hover);\n}\r\n", "",{"version":3,"sources":["webpack://./src/components/InvoicePaymentModal.vue"],"names":[],"mappings":";AAkQA;CACC,eAAe;CACf,MAAM;CACN,OAAO;CACP,QAAQ;CACR,SAAS;CACT,oCAAoC;CACpC,aAAa;CACb,mBAAmB;CACnB,uBAAuB;CACvB,cAAc;CACd,aAAa;AACd;AAEA;CACC,8CAA8C;CAC9C,6CAA6C;CAC7C,yCAAyC;CACzC,gBAAgB;CAChB,WAAW;CACX,gBAAgB;CAChB,aA