d-dark);\r\n\tcolor: var(--color-main-text);\r\n\tfont-size: 14px;\r\n\tbox-sizing: border-box;\n}\n.form-control[data-v-c11aae1c]:focus {\r\n\toutline: none;\r\n\tborder-color: var(--color-primary);\n}\n.form-control[data-v-c11aae1c]::placeholder {\r\n\tcolor: var(--color-text-maxcontrast);\n}\n.form-actions[data-v-c11aae1c] {\r\n\tdisplay: flex;\r\n\tjustify-content: flex-end;\r\n\tgap: 12px;\r\n\tmargin-top: 24px;\r\n\tpadding-top: 20px;\r\n\tborder-top: 1px solid var(--color-border);\n}\n.button-vue[data-v-c11aae1c] {\r\n\tdisplay: inline-flex;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n\tpadding: 8px 16px;\r\n\tborder-radius: var(--border-radius-small);\r\n\tfont-size: 14px;\r\n\tcursor: pointer;\r\n\ttransition: all 0.2s ease;\r\n\ttext-decoration: none;\r\n\twhite-space: nowrap;\r\n\tbox-sizing: border-box;\r\n\tborder: 1px solid transparent;\n}\n.button-vue[data-v-c11aae1c]:disabled {\r\n\topacity: 0.6;\r\n\tcursor: not-allowed;\n}\n.button-vue--primary[data-v-c11aae1c] {\r\n\tbackground-color: var(--color-primary);\r\n\tcolor: var(--color-primary-text);\n}\n.button-vue--primary[data-v-c11aae1c]:hover:not(:disabled) {\r\n\tbackground-color: var(--color-primary-hover);\n}\n.button-vue--secondary[data-v-c11aae1c] {\r\n\tbackground-color: transparent;\r\n\tcolor: var(--color-main-text);\r\n\tborder-color: var(--color-border);\n}\n.button-vue--secondary[data-v-c11aae1c]:hover:not(:disabled) {\r\n\tbackground-color: var(--color-background-hover);\n}\n.button-vue .material-icon[data-v-c11aae1c] {\r\n\tmargin-right: 8px;\n}\n.contacts-list[data-v-c11aae1c] {\r\n\tmax-height: 500px;\r\n\toverflow-y: auto;\n}\n.contact-item[data-v-c11aae1c] {\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tpadding: 12px;\r\n\tborder-bottom: 1px solid var(--color-border);\r\n\tcursor: pointer;\r\n\ttransition: background-color 0.2s;\n}\n.contact-item[data-v-c11aae1c]:hover {\r\n\tbackground-color: var(--color-background-hover);\n}\n.contact-item[data-v-c11aae1c]:las