t// Update existing item - would need update endpoint\r\n\t\t\t\t\t// For now, we'll delete and recreate\r\n\t\t\t\t\tawait api.invoices.removeItem(this.formData.invoiceId, this.item.id)\r\n\t\t\t\t}\r\n\t\t\t\tawait api.invoices.addItem(this.formData.invoiceId, data)\r\n\t\t\t\tthis.$emit('saved')\r\n\t\t\t\tthis.closeModal()\r\n\t\t\t} catch (error) {\r\n\t\t\t\tconsole.error('Error saving item:', error)\r\n\t\t\t\talert(this.translate('domaincontrol', 'Error saving item'))\r\n\t\t\t} finally {\r\n\t\t\t\tthis.saving = false\r\n\t\t\t}\r\n\t\t},\r\n\t\tcloseModal() {\r\n\t\t\tthis.isOpen = false\r\n\t\t\tthis.$emit('close')\r\n\t\t},\r\n\t\ttranslate(appId, text, vars) {\r\n\t\t\ttry {\r\n\t\t\t\tif (typeof window !== 'undefined') {\r\n\t\t\t\t\tif (typeof OC !== 'undefined' && OC.L10n && typeof OC.L10n.translate === 'function') {\r\n\t\t\t\t\t\tconst translated = OC.L10n.translate(appId, text, vars || {})\r\n\t\t\t\t\t\tif (translated && translated !== text) {\r\n\t\t\t\t\t\t\treturn translated\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t\tif (typeof window.t === 'function') {\r\n\t\t\t\t\t\tconst translated = window.t(appId, text, vars || {})\r\n\t\t\t\t\t\tif (translated && translated !== text) {\r\n\t\t\t\t\t\t\treturn translated\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t}\r\n\t\t\t} catch (e) {\r\n\t\t\t\tconsole.warn('Translation error:', e)\r\n\t\t\t}\r\n\r\n\t\t\tconst translations = {\r\n\t\t\t\t'Edit Item': 'Kalem Dzenle',\r\n\t\t\t\t'Add Item': 'Kalem Ekle',\r\n\t\t\t\t'Description': 'Aklama',\r\n\t\t\t\t'e.g., Annual domain renewal': 'rn: Yllk domain yenileme',\r\n\t\t\t\t'Quantity': 'Miktar',\r\n\t\t\t\t'Unit Price': 'Birim Fiyat',\r\n\t\t\t\t'Total Price': 'Toplam Fiyat',\r\n\t\t\t\t'Period Start': 'Dnem Balang',\r\n\t\t\t\t'Period End': 'Dnem Biti',\r\n\t\t\t\t'Cancel': 'ptal',\r\n\t\t\t\t'Saving...': 'Kaydediliyor...',\r\n\t\t\t\t'Save': 'Kaydet',\r\n\t\t\t\t'Error saving item': 'Kalem kaydedilirken hata olutu',\r\n\t\t\t}\r\n\r\n\t\t\treturn trans