___CSS_LOADER_EXPORT___.push([module.id, "\n.modal-overlay[data-v-1e3e47dc] {\r\n\tposition: fixed;\r\n\ttop: 0;\r\n\tleft: 0;\r\n\tright: 0;\r\n\tbottom: 0;\r\n\tbackground-color: rgba(0, 0, 0, 0.5);\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n\tz-index: 10000;\r\n\tpadding: 20px;\n}\n.modal-content[data-v-1e3e47dc] {\r\n\tbackground-color: var(--color-main-background);\r\n\tborder-radius: var(--border-radius-container);\r\n\tbox-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);\r\n\tmax-width: 500px;\r\n\twidth: 100%;\r\n\tmax-height: 85vh;\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\toverflow: hidden;\n}\n.modal-header[data-v-1e3e47dc] {\r\n\tdisplay: flex;\r\n\tjustify-content: space-between;\r\n\talign-items: center;\r\n\tpadding: 20px;\r\n\tborder-bottom: 1px solid var(--color-border);\n}\n.modal-title[data-v-1e3e47dc] {\r\n\tmargin: 0;\r\n\tfont-size: 20px;\r\n\tfont-weight: 600;\r\n\tcolor: var(--color-main-text);\n}\n.modal-close[data-v-1e3e47dc] {\r\n\tbackground: none;\r\n\tborder: none;\r\n\tfont-size: 28px;\r\n\tcolor: var(--color-text-maxcontrast);\r\n\tcursor: pointer;\r\n\tpadding: 0;\r\n\twidth: 32px;\r\n\theight: 32px;\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n\tborder-radius: var(--border-radius-small);\r\n\ttransition: background-color 0.2s;\n}\n.modal-close[data-v-1e3e47dc]:hover {\r\n\tbackground-color: var(--color-background-hover);\n}\n.modal-body[data-v-1e3e47dc] {\r\n\tpadding: 20px;\r\n\toverflow-y: auto;\r\n\tflex: 1;\r\n\tmin-height: 0;\n}\n.payment-info[data-v-1e3e47dc] {\r\n\tbackground-color: var(--color-background-hover);\r\n\tborder-radius: var(--border-radius-small);\r\n\tpadding: 16px;\r\n\tmargin-bottom: 20px;\r\n\tborder: 1px solid var(--color-border);\n}\n.payment-info p[data-v-1e3e47dc] {\r\n\tmargin: 8px 0;\r\n\tcolor: var(--color-main-text);\n}\n.text-success[data-v-1e3e47dc] {\r\n\tcolor: var(--color-success);\r\n\tfont-weight: 600;\n}\n.form-row[data-v-1e3e47dc] {\r\n\tdisplay: grid;\r\n\tgrid-template-columns: repeat(2, 1fr);\r\n\tgap: 16px;\r\n\tmargin-bottom: 16px;\n}\n.form-group[data-v-1e3e47dc] {\r\n\tmargin-bottom: 16px;\n}\n.form-group label[data-v-1e3e47dc] {\r\n\tdisplay: block;\r\n\tmargin-bottom: 6px;\r\n\tfont-weight: 500;\r\n\tcolor: var(--color-main-text);\n}\n.form-control[data-v-1e3e47dc] {\r\n\twidth: 100%;\r\n\tpadding: 8px 12px;\r\n\tborder: 1px solid var(--color-border);\r\n\tborder-radius: var(--border-radius-element);\r\n\tbackground-color: var(--color-background-dark);\r\n\tcolor: var(--color-main-text);\r\n\tfont-size: 14px;\r\n\tbox-sizing: border-box;\n}\n.form-control[data-v-1e3e47dc]:focus {\r\n\toutline: none;\r\n\tborder-color: var(--color-primary);\n}\n.form-control[data-v-1e3e47dc]::placeholder {\r\n\tcolor: var(--color-text-maxcontrast);\n}\n.form-actions[data-v-1e3e47dc] {\r\n\tdisplay: flex;\r\n\tjustify-content: flex-end;\r\n\tgap: 12px;\r\n\tmargin-top: 24px;\r\n\tpadding-top: 20px;\r\n\tborder-top: 1px solid var(--color-border);\r\n\tposition: sticky;\r\n\tbottom: 0;\r\n\tbackground-color: var(--color-main-background);\r\n\tz-index: 10;\n}\n.button-vue[data-v-1e3e47dc] {\r\n\tdisplay: inline-flex;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n\tpadding: 8px 16px;\r\n\tborder-radius: var(--border-radius-small);\r\n\tfont-size: 14px;\r\n\tcursor: pointer;\r\n\ttransition: all 0.2s ease;\r\n\ttext-decoration: none;\r\n\twhite-space: nowrap;\r\n\tbox-sizing: border-box;\r\n\tborder: 1px solid transparent;\n}\n.button-vue[data-v-1e3e47dc]:disabled {\r\n\topacity: 0.6;\r\n\tcursor: not-allowed;\n}\n.button-vue--primary[data-v-1e3e47dc] {\r\n\tbackground-color: var(--color-primary);\r\n\tcolor: var(--color-primary-text);\n}\n.button-vue--primary[data-v-1e3e47dc]:hover:not(:disabled) {\r\n\tbackground-color: var(--color-primary-hover);\n}\n.button-vue--secondary[data-v-1e3e47dc] {\r\n\tbackground-color: transparent;\r\n\tcolor: var(--color-main-text);\r\n\tborder-color: var(--color-border);\n}\n.button-vue--secondary[data-v-1e3e47dc]:hover:not(:disabled) {\r\n\tbackground-color: var(--color-background-hover);\n}\n.button-vue--success[data-v-1e3e47dc] {\r\n\tbackground-color: var(--color-success);\r\n\tcolor: var(--color-success-text);\n}\n.button-vue--success[data-v-1e3e47dc]:hover:not(:disabled) {\r\n\tbackground-color: var(--color-success-hover);\n}\r\n", "",{"version":3,"sources":["webpack://./src/components/HostingPaymentModal.vue"],"names":[],"mappings":";AAwNA;CACC,eAAe;CACf,MAAM;CACN,OAAO;CACP,QAAQ;CACR,SAAS;CACT,oCAAoC;CACpC,aAAa;CACb,mBAAmB;CACnB,uBAAuB;CACvB,cAAc;CACd,aAAa;AACd;AAEA;CACC,8CAA8C;CAC9C,6CAA6C;CAC7C,yCAAyC;CACzC,gBAAgB;CAChB,WAAW;CACX,gBAAgB;CAChB,aAAa;CACb,sBAAsB;CACtB,gBAAgB;AACjB;AAEA;CACC,aAAa;CACb,8BAA8B;CAC9B,mBAAmB;CACnB,aAAa;CACb,4CAA4C;AAC7C;AAEA;CACC,SAAS;CACT,eAAe;CACf,gBAAgB;CAChB,6BAA6B;AAC9B;AAEA;CACC,gBAAgB;CAChB,YAAY;CACZ,eAAe;CACf,oCAAoC;CACpC,eAAe;CACf,UAAU;CACV,WAAW;CACX,YAAY;CACZ,aAAa;CACb,mBAAmB;CACnB,uBAAuB;CACvB,yCAAyC;CACzC,iCAAiC;AAClC;AAEA;CACC,+CAA+C;AAChD;AAEA;CACC,aAAa;CACb,gBAAgB;CAChB,OAAO;CACP,aAAa;AACd;AAEA;CACC,+CAA+C;CAC/C,yCAAyC;CACzC,aAAa;CACb,mBAAmB;CACnB,qCAAqC;AACtC;AAEA;CACC,aAAa;CACb,6BAA6B;AAC9B;AAEA;CACC,2BAA2B;CAC3B,gBAAgB;AACjB;AAEA;CACC,aAAa;CACb,qCAAqC;CACrC,SAAS;CACT,mBAAmB;AACpB;AAEA;CACC,mBAAmB;AACpB;AAEA;CACC,cAAc;CACd,kBAAkB;CAClB,gBAAgB;CAChB,6BAA6B;AAC9B;AAEA;CACC,WAAW;CACX,iBAAiB;CACjB,qCAAqC;CACrC,2CAA2C;CAC3C,8CAA8C;CAC9C,6BAA6B;CAC7B,eAAe;CACf,sBAAsB;AACvB;AAEA;CACC,aAAa;CACb,kCAAkC;AACnC;AAEA;CACC,oCAAoC;AACrC;AAEA;CACC,aAAa;CACb,yBAAyB;CACzB,SAAS;CACT,gBAAgB;CAChB,iBAAiB;CACjB,yCAAyC;CACzC,gBAAgB;CAChB,SAAS;CACT,8CAA8C;CAC9C,WAAW;AACZ;AAEA;CACC,oBAAoB;CACpB,mBAAmB;CACnB,uBAAuB;CACvB,iBAAiB;CACjB,yCAAyC;CACzC,eAAe;CACf,eAAe;CACf,yBAAyB;CACzB,qBAAqB;CACrB,mBAAmB;CACnB,sBAAsB;CACtB,6BAA6B;AAC9B;AAEA;CACC,YAAY;CACZ,mBAAmB;AACpB;AAEA;CACC,sCAAsC;CACtC,gCAAgC;AACjC;AAEA;CACC,4CAA4C;AAC7C;AAEA;CACC,6BAA6B;CAC7B,6BAA6B;CAC7B,iCAAiC;AAClC;AAEA;CACC,+CAA+C;AAChD;AAEA;CACC,sCAAsC;CACtC,gCAAgC;AACjC;AAEA;CACC,4CAA4C;AAC7C","sourcesContent":["<template>\r\n\t<div v-if=\"isOpen\" class=\"modal-overlay\" @click.self=\"closeModal\">\r\n\t\t<div class=\"modal-content\">\r\n\t\t\t<div class=\"modal-header\">\r\n\t\t\t\t<h3 class=\"modal-title\">{{ translate('domaincontrol', 'Add Hosting Payment') }}</h3>\r\n\t\t\t\t<button class=\"modal-close\" @click=\"closeModal\">&times;</button>\r\n\t\t\t</div>\r\n\t\t\t<div class=\"modal-body\">\r\n\t\t\t\t<div v-if=\"hosting\" class=\"payment-info\">\r\n\t\t\t\t\t<p><strong>{{ translate('domaincontrol', 'Hosting') }}:</strong> {{ hosting.provider }} {{ hosting.plan ? `- ${hosting.plan}` : '' }}</p>\r\n\t\t\t\t\t<p><strong>{{ translate('domaincontrol', 'Current Expiry') }}:</strong> {{ formatDate(hosting.expirationDate) || '-' }}</p>\r\n\t\t\t\t\t<p><strong>{{ translate('domaincontrol', 'New Expiry') }}:</strong> <span class=\"text-success\">{{ formatNewExpiry() }}</span></p>\r\n\t\t\t\t</div>\r\n\t\t\t\t<form @submit.prevent=\"savePayment\">\r\n\t\t\t\t\t<div class=\"form-row\">\r\n\t\t\t\t\t\t<div class=\"form-group\">\r\n\t\t\t\t\t\t\t<label for=\"hp-amount\">{{ translate('domaincontrol', 'Payment Amount') }}</label>\r\n\t\t\t\t\t\t\t<input\r\n\t\t\t\t\t\t\t\ttype=\"number\"\r\n\t\t\t\t\t\t\t\tid=\"hp-amount\"\r\n\t\t\t\t\t\t\t\tv-model=\"formData.amount\"\r\n\t\t\t\t\t\t\t\tstep=\"0.01\"\r\n\t\t\t\t\t\t\t\tclass=\"form-control\"\r\n\t\t\t\t\t\t\t\tplaceholder=\"9.99\"\r\n\t\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t<div class=\"form-group\">\r\n\t\t\t\t\t\t\t<label for=\"hp-currency\">{{ translate('domaincontrol', 'Currency') }}</label>\r\n\t\t\t\t\t\t\t<select id=\"hp-currency\" v-model=\"formData.currency\" class=\"form-control\">\r\n\t\t\t\t\t\t\t\t<option value=\"USD\">$ USD</option>\r\n\t\t\t\t\t\t\t\t<option value=\"EUR\"> EUR</option>\r\n\t\t\t\t\t\t\t\t<option value=\"TRY\"> TRY</option>\r\n\t\t\t\t\t\t\t\t<option value=\"AZN\"> AZN</option>\r\n\t\t\t\t\t\t\t\t<option value=\"GBP\"> GBP</option>\r\n\t\t\t\t\t\t\t\t<option value=\"RUB\"> RUB</option>\r\n\t\t\t\t\t\t\t</select>\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div class=\"form-group\">\r\n\t\t\t\t\t\t<label for=\"hp-period\">{{ translate('domaincontrol', 'Payment Period') }}</label>\r\n\t\t\t\t\t\t<select id=\"hp-period\" v-model=\"formData.period\" class=\"form-control\">\r\n\t\t\t\t\t\t\t<option value=\"1\">1 {{ translate('domaincontrol', 'Month') }}</option>\r\n\t\t\t\t\t\t\t<option value=\"3\">3 {{ translate('domaincontrol', 'Months') }}</option>\r\n\t\t\t\t\t\t\t<option value=\"6\">6 {{ translate('domaincontrol', 'Months') }}</option>\r\n\t\t\t\t\t\t\t<option value=\"12\">1 {{ translate('domaincontrol', 'Year') }}</option>\r\n\t\t\t\t\t\t\t<option value=\"24\">2 {{ translate('domaincontrol', 'Years') }}</option>\r\n\t\t\t\t\t\t</select>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div class=\"form-group\">\r\n\t\t\t\t\t\t<label for=\"hp-note\">{{ translate('domaincontrol', 'Note') }}</label>\r\n\t\t\t\t\t\t<textarea\r\n\t\t\t\t\t\t\tid=\"hp-note\"\r\n\t\t\t\t\t\t\tv-model=\"formData.note\"\r\n\t\t\t\t\t\t\tclass=\"form-control\"\r\n\t\t\t\t\t\t\trows=\"2\"\r\n\t\t\t\t\t\t\t:placeholder=\"translate('domaincontrol', 'Payment note...')\"\r\n\t\t\t\t\t\t></textarea>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div class=\"form-actions\">\r\n\t\t\t\t\t\t<button type=\"button\" class=\"button-vue button-vue--secondary\" @click=\"closeModal\">\r\n\t\t\t\t\t\t\t{{ translate('domaincontrol', 'Cancel') }}\r\n\t\t\t\t\t\t</button>\r\n\t\t\t\t\t\t<button type=\"submit\" class=\"button-vue button-vue--success\" :disabled=\"saving\">\r\n\t\t\t\t\t\t\t{{ saving ? translate('domaincontrol', 'Saving...') : translate('domaincontrol', 'Save Payment') }}\r\n\t\t\t\t\t\t</button>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t</form>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</div>\r\n</template>\r\n\r\n<script>\r\nimport api from '../services/api'\r\n\r\nexport default {\r\n\tname: 'HostingPaymentModal',\r\n\tprops: {\r\n\t\topen: {\r\n\t\t\ttype: Boolean,\r\n\t\t\tdefault: false,\r\n\t\t},\r\n\t\thosting: {\r\n\t\t\ttype: Object,\r\n\t\t\tdefault: null,\r\n\t\t},\r\n\t},\r\n\temits: ['close', 'paid'],\r\n\tdata() {\r\n\t\treturn {\r\n\t\t\tisOpen: false,\r\n\t\t\tformData: {\r\n\t\t\t\tamount: null,\r\n\t\t\t\tcurrency: 'USD',\r\n\t\t\t\tperiod: '12',\r\n\t\t\t\tnote: '',\r\n\t\t\t},\r\n\t\t\tsaving: false,\r\n\t\t}\r\n\t},\r\n\twatch: {\r\n\t\topen(newVal) {\r\n\t\t\tthis.isOpen = newVal\r\n\t\t\tif (newVal) {\r\n\t\t\t\tthis.resetForm()\r\n\t\t\t\tif (this.hosting && this.hosting.currency) {\r\n\t\t\t\t\tthis.formData.currency = this.hosting.currency\r\n\t\t\t\t}\r\n\t\t\t}\r\n\t\t},\r\n\t},\r\n\tmethods: {\r\n\t\ttranslate(appId, text, vars) {\r\n\t\t\ttry {\r\n\t\t\t\tif (typeof window !== 'undefined') {\r\n\t\t\t\t\tif (typeof OC !== 'undefined' && OC.L10n && typeof OC.L10n.translate === 'function') {\r\n\t\t\t\t\t\tconst translated = OC.L10n.translate(appId, text, vars || {})\r\n\t\t\t\t\t\tif (translated && translated !== text) {\r\n\t\t\t\t\t\t\treturn translated\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t\tif (typeof window.t === 'function') {\r\n\t\t\t\t\t\tconst translated = window.t(appId, text, vars || {})\r\n\t\t\t\t\t\tif (translated && translated !== text) {\r\n\t\t\t\t\t\t\treturn translated\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t}\r\n\t\t\t} catch (e) {\r\n\t\t\t\tconsole.warn('Translation error:', e)\r\n\t\t\t}\r\n\r\n\t\t\tconst translations = {\r\n\t\t\t\t'Add Hosting Payment': 'Hosting deme Ekle',\r\n\t\t\t\t'Hosting': 'Hosting',\r\n\t\t\t\t'Current Expiry': 'Mevcut Biti',\r\n\t\t\t\t'New Expiry': 'Yeni Biti',\r\n\t\t\t\t'Payment Amount': 'deme Tutar',\r\n\t\t\t\t'Currency': 'Para Birimi',\r\n\t\t\t\t'Payment Period': 'deme Periyodu',\r\n\t\t\t\t'Month': 'Ay',\r\n\t\t\t\t'Months': 'Ay',\r\n\t\t\t\t'Year': 'Yl',\r\n\t\t\t\t'Years': 'Yl',\r\n\t\t\t\t'Note': 'Not',\r\n\t\t\t\t'Payment note...': 'deme notu...',\r\n\t\t\t\t'Cancel': 'ptal',\r\n\t\t\t\t'Saving...': 'Kaydediliyor...',\r\n\t\t\t\t'Save Payment': 'demeyi Kaydet',\r\n\t\t\t\t'Error saving payment': 'deme kaydedilirken hata olutu',\r\n\t\t\t}\r\n\r\n\t\t\treturn translations[text] || text\r\n\t\t},\r\n\t\tresetForm() {\r\n\t\t\tthis.formData = {\r\n\t\t\t\tamount: null,\r\n\t\t\t\tcurrency: this.hosting?.currency || 'USD',\r\n\t\t\t\tperiod: '12',\r\n\t\t\t\tnote: '',\r\n\t\t\t}\r\n\t\t},\r\n\t\tcloseModal() {\r\n\t\t\tthis.$emit('close')\r\n\t\t\tthis.resetForm()\r\n\t\t},\r\n\t\tformatDate(dateString) {\r\n\t\t\tif (!dateString) return '-'\r\n\t\t\ttry {\r\n\t\t\t\tconst options = { year: 'numeric', month: 'long', day: 'numeric' }\r\n\t\t\t\treturn new Date(dateString).toLocaleDateString(undefined, options)\r\n\t\t\t} catch (e) {\r\n\t\t\t\treturn dateString.split(' ')[0]\r\n\t\t\t}\r\n\t\t},\r\n\t\tformatNewExpiry() {\r\n\t\t\tif (!this.hosting || !this.hosting.expirationDate) return '-'\r\n\t\t\ttry {\r\n\t\t\t\tconst currentDate = new Date(this.hosting.expirationDate)\r\n\t\t\t\tconst months = parseInt(this.formData.period) || 12\r\n\t\t\t\tconst newDate = new Date(currentDate)\r\n\t\t\t\tnewDate.setMonth(newDate.getMonth() + months)\r\n\t\t\t\tconst options = { year: 'numeric', month: 'long', day: 'numeric' }\r\n\t\t\t\treturn newDate.toLocaleDateString(undefined, options)\r\n\t\t\t} catch (e) {\r\n\t\t\t\treturn '-'\r\n\t\t\t}\r\n\t\t},\r\n\t\tasync savePayment() {\r\n\t\t\tif (!this.hosting) return\r\n\r\n\t\t\tthis.saving = true\r\n\t\t\ttry {\r\n\t\t\t\tconst data = {\r\n\t\t\t\t\tamount: this.formData.amount || null,\r\n\t\t\t\t\tcurrency: this.formData.currency,\r\n\t\t\t\t\tperiod: this.formData.period,\r\n\t\t\t\t\tnote: this.formData.note || '',\r\n\t\t\t\t}\r\n\r\n\t\t\t\tawait api.hostings.addPayment(this.hosting.id, data)\r\n\r\n\t\t\t\tthis.closeModal()\r\n\t\t\t\tthis.$emit('paid')\r\n\t\t\t} catch (error) {\r\n\t\t\t\tconsole.error('Error saving payment:', error)\r\n\t\t\t\talert(this.translate('domaincontrol', 'Error saving payment'))\